<template>
  <div class="bg">
    <div>
      <el-button style="margin: 20px" @click="handleImport">导入</el-button>
      <el-button style="margin: 20px" v-print="'#printView'">打印</el-button>
      <el-button style="margin: 20px" @click="handleCz">重置</el-button>
      <input
        type="file"
        ref="uploadBtn"
        style="display: none"
        accept=".xls,.xlsx"
        @change="readExcel"
        class="upload-button"
      />
    </div>
    <div id="printView" v-loading="loading">
      <div class="page" v-for="(page, i1) in tableData" :key="i1">
        <div class="page-item" v-for="(item, i) in page" :key="i">
          <div class="brcode">
            <barcode
              :value="item.code"
              :options="{
                width: 2.25,
                height: 150,
                fontSize: 16,
                margin: 0,
                background: 'rgba(255,255,255,.0)',
              }"
            ></barcode>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as XLSX from "xlsx/xlsx.mjs";
export default {
  name: "Home",
  data() {
    return {
      loading: false,
      excelData: "",
      tableData: [
        [
          { code: "32CSJ16001" },
          { code: "32CSJ16002" },
          { code: "32CSJ16003" },
          { code: "32CSJ16004" },
          { code: "32CSJ16005" },
          { code: "32CSJ16006" },
          { code: "32CSJ16007" },
          { code: "32CSJ16008" },
          { code: "32CSJ16009" },
        ],
      ],
    };
  },
  created() {},
  methods: {
    handleCz() {
      this.tableData = [
        [
          { code: "32CSJ16001" },
          { code: "32CSJ16002" },
          { code: "32CSJ16003" },
          { code: "32CSJ16004" },
          { code: "32CSJ16005" },
          { code: "32CSJ16006" },
          { code: "32CSJ16007" },
          { code: "32CSJ16008" },
          { code: "32CSJ16009" },
        ],
      ];
    },
    handleImport() {
      this.$refs.uploadBtn.click();
    },
    readExcel(e) {
      this.loading = true;
      const files = e.target.files;

      const fileReader = new FileReader();
      fileReader.onload = (ev) => {
        try {
          const data = ev.target.result;
          const workbook = XLSX.read(data, {
            type: "binary",
          }); // 读取数据
          const wsname = workbook.SheetNames[0]; // 取第一张表
          const ws = XLSX.utils.sheet_to_json(workbook.Sheets[wsname]); // 生成json表格内容
          // const ws1 = XLSX.utils.sheet_to_slk(workbook.Sheets[wsname]) // 输出表格对应位置是什么值
          // const ws2 = XLSX.utils.sheet_to_html(workbook.Sheets[wsname]) // 生成HTML输出
          // const ws3 = XLSX.utils.sheet_to_csv(workbook.Sheets[wsname]) // 生成分隔符分隔值输出
          // const ws4 = XLSX.utils.sheet_to_formulae(workbook.Sheets[wsname]) // 生成公式列表（具有值回退）
          // const ws5 = XLSX.utils.sheet_to_txt(workbook.Sheets[wsname]) // 生成UTF16格式的文本
          // const ws = xlsx.utils.sheet_to_json(workbook.Sheets[wsname], {
          //   header: 1,
          //   defval: "",
          // }); // 生成json表格内容
          // console.log(ws);
          const arr = ws;
          this.tableData = this.pages(arr, 9);
          this.excelData = []; // 清空接收数据
          this.loading = false;
          if (this.excelData.length) {
            this.fileName = files[0].name;
          }
        } catch (e) {
          return false;
        }
      };
      fileReader.readAsBinaryString(files[0]);
    },
    pages(arr, len) {
      const pages = [];
      arr.forEach((item, index) => {
        const page = Math.floor(index / len);
        if (!pages[page]) {
          pages[page] = [];
        }
        pages[page].push(item);
      });
      return pages;
    },
  },
};
</script>

<style lang="less" scoped>
.bg {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-color: #eee;
}
.page {
  font-size: 0;
  padding: 20px;
  height: 210mm;
  width: 297mm;
  background-color: #fff;
  // border: 1px solid #000;
}
.page-item {
  position: relative;
  display: inline-block;
  width: 90mm;
  height: 55mm;
  border: 0.1px solid #000;
  margin: 0;
}
.page-item:nth-child(2) {
  border-left: unset;
  border-right: unset;
}
.page-item:nth-child(8) {
  border-left: unset;
  border-right: unset;
}
.page-item:nth-child(5) {
  border: unset;
}
.page-item:nth-child(4) {
  border-top: unset;
  border-bottom: unset;
}
.page-item:nth-child(6) {
  border-top: unset;
  border-bottom: unset;
}
.brcode {
  position: absolute;
  width: 80mm;
  height: 45mm;
  // border: 0.1px solid #000;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
@media print {
  .printView {
    height: 210mm;
    width: 297mm;
    margin: 0;
  }
}
</style>
