import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/home/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    meta: { index: 1 },
    component: Home,
  },
  {
    path: "/SecretBase",
    name: "SecretBase",
    meta: { index: 2 },
    component: () => import("../views/secretBase/SecretBase.vue"),
  },
  {
    path: "/Parallax",
    name: "Parallax",
    meta: { index: 3 },
    component: () => import("../views/parallax/Parallax.vue"),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
