import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isToolbarShow: false,
    isNavDialogShow: false
  },
  mutations: {
    updateToolbarShow (state, bool) {
      state.isToolbarShow = bool
    },
    updateNavDialogShow (state, bool) {
      state.isNavDialogShow = bool
    }
  },
  actions: {
  },
  modules: {
  }
})
